<template>
  <div class="listing">
    <div class="card" style="width: 30rem;">
      <div v-if="image">
        <img class="card-img-top" :src="image.url" alt="Card image cap" />
      </div>
      <div v-else>
        <img class="card-img-top" src="../assets/house.jpg" alt="Card image cap" />
      </div>
      <div class="card-body">
        <h5 class="card-title">{{ title }}</h5>
        <p>Price: {{ weiPrice }} wei</p>
        <p>Contact: {{contactInfo}}</p>
        <p>Location: {{get_street_address}}</p>
        <p class="card-text">Description: {{ description }}</p>
        <div v-if="!editable">
          <div v-if="!booked">
            <div class="form-row">
              <div class="col">
                <input
                  v-model="start_time"
                  type="text"
                  class="form-control"
                  placeholder="YYYY-MM-DD"
                />
              </div>
              <div class="col">
                <input v-model="end_time" type="text" class="form-control" placeholder="YYYY-MM-DD" />
              </div>
            </div>
          </div>
          <div v-else>
            <div class="form-row">
              <div class="col">
                <input disabled type="text" class="form-control" placeholder="YYYY-MM-DD" />
              </div>
              <div class="col">
                <input disabled type="text" class="form-control" placeholder="YYYY-MM-DD" />
              </div>
            </div>
          </div>
          <br />
          <button
            v-if="!booked"
            type="button"
            class="btn btn-primary"
            v-on:click="book_listing"
          >Book</button>
          <button v-else type="button" class="btn btn-danger" style="cursor:default" disabled>BOOKED</button>
        </div>
        <div v-else>
          <button type="button" class="btn btn-danger" v-on:click="delete_listing">Delete</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { LISTING_ABI, MANAGER_ABI, MANAGER_ADDRESS } from "@/util/constants.js";

export default {
  name: "Listing",
  data() {
    return {
      start_time: null,
      end_time: null
    };
  },
  props: {
    title: String,
    renter: String,
    weiPrice: Number,
    contactInfo: String,
    description: String,
    address: String,
    editable: Boolean,
    booked: Boolean,
    image: Object,
    streetAddress: Object
  },
  computed: {
    account_id() {
      return this.$store.state.account_id;
    },

    get_street_address() {
      let out = "";
      out += this.streetAddress["line1"]
        ? this.streetAddress["line1"] + ", "
        : "";
      out += this.streetAddress["line2"]
        ? this.streetAddress["line2"] + ", "
        : "";
      out += this.streetAddress["city"]
        ? this.streetAddress["city"] + ", "
        : "";
      out += this.streetAddress["state"]
        ? this.streetAddress["state"] + ", "
        : "";
      out += this.streetAddress["zipcode"] ? this.streetAddress["zipcode"] : "";
      return out;
    }
  },
  methods: {
    book_listing: function() {
      let start = new Date(this.start_time);
      let end = new Date(this.end_time);

      // Validate date input
      if (
        start.getTime() == start.getTime() &&
        end.getTime() == end.getTime() &&
        start.getTime() < end.getTime()
      ) {
        const listing_contract = new window.web3.eth.Contract(
          LISTING_ABI,
          this.address,
          {
            from: this.account_id,
            gasPrice: "10000000" // Could modify this
          }
        );

        listing_contract.methods
          .addBooking(this.contactInfo, start.getTime(), end.getTime())
          .send({ from: this.account_id })
          .on("receipt", receipt => {
            console.log("booked listing:", receipt);
            this.$store.dispatch("update_listings");
          })
          .on("error", (error, receipt) => {
            console.log("error booking listing:", error);
            console.log("error_receipt:", receipt);
          });
      } else {
        alert("Please provide valid dates in the format of YYYY-MM-DD");
      }
    },

    delete_listing: function() {
      const manager_contract = new window.web3.eth.Contract(
        MANAGER_ABI,
        MANAGER_ADDRESS,
        {
          from: this.account_id,
          gasPrice: "10000000" // Could modify this
        }
      );

      manager_contract.methods
        .removeListing(this.address)
        .send({ from: this.account_id })
        .on("receipt", receipt => {
          console.log("deleted listing:", receipt);
          this.$store.dispatch("update_listings");
        })
        .on("error", (error, receipt) => {
          console.log("error deleting listing:", error);
          console.log("error_receipt:", receipt);
        });
    }
  }
};
</script>

<style scoped>
.card {
  margin: 10px auto; /* Added */
  float: none; /* Added */
}
</style>