<template>
  <div class="listings">
    <h4>Create a Listing</h4>
    <form>
      <div class="form-group">
        <label for="title">Title</label>
        <input v-model="title" class="form-control" id="title" required />
      </div>
      <div class="form-group">
        <label for="image_url">Image URL</label>
        <input v-model="image_url" class="form-control" id="image_url" required />
      </div>
      <div class="form-row">
        <div class="form-group col-md-6">
          <label for="price">Price</label>
          <input v-model="price" class="form-control" id="price" required />
        </div>
        <div class="form-group col-md-6">
          <label for="contact">Contact</label>
          <input v-model="contact" class="form-control" id="contact" required />
        </div>
      </div>
      <div class="form-group">
        <label for="inputAddress">Address 1</label>
        <input v-model="address1" type="text" class="form-control" id="inputAddress" required />
      </div>
      <div class="form-group">
        <label for="inputAddress2">Address 2</label>
        <input v-model="address2" type="text" class="form-control" id="inputAddress2" required />
      </div>
      <div class="form-row">
        <div class="form-group col-md-4">
          <label for="inputCity">City</label>
          <input v-model="city" type="text" class="form-control" id="inputCity" required />
        </div>
        <div class="form-group col-md-4">
          <label for="inputState">State</label>
          <input v-model="state" type="text" class="form-control" required />
        </div>
        <div class="form-group col-md-4">
          <label for="inputZip">Zip</label>
          <input v-model="zip" type="text" class="form-control" id="inputZip" required />
        </div>
      </div>
      <div class="form-group">
        <label for="description">Description</label>
        <textarea v-model="description" class="form-control" id="description" rows="7" required></textarea>
      </div>
      <button
        type="button"
        style="margin-bottom:20px"
        class="btn btn-primary"
        v-on:click="create_listing"
      >Add Listing</button>
    </form>
  </div>
</template>

<script>
import { MANAGER_ABI, LISTING_ABI, MANAGER_ADDRESS } from "@/util/constants.js";

export default {
  data() {
    return {
      title: null,
      image_url: null,
      price: null,
      contact: null,
      address1: null,
      address2: null,
      city: null,
      state: null,
      zip: null,
      description: null
    };
  },

  computed: {
    account_id() {
      return this.$store.state.account_id;
    }
  },

  methods: {
    async create_listing() {
      if (
        this.title &&
        this.image_url &&
        this.price &&
        this.contact &&
        this.address1 &&
        this.city &&
        this.state &&
        this.zip &&
        this.description
      ) {
        try {
          // Add a new listing
          const manager_contract = new window.web3.eth.Contract(
            MANAGER_ABI,
            MANAGER_ADDRESS,
            {
              from: this.account_id,
              gasPrice: "10000000" // Could modify this but it works too frickin slow
            }
          );
          let add_receipt = await manager_contract.methods
            .addListing(this.contact, this.price, this.title, this.description)
            .send({ from: this.account_id });
          let new_listing =
            add_receipt.events.ListingReturn.returnValues.listing;
          console.log("added new listing: " + new_listing);

          // Add an image to the listing
          let listing_contract = new window.web3.eth.Contract(
            LISTING_ABI,
            "" + new_listing,
            {
              from: this.account_id,
              gasPrice: "10000000"
            }
          );
          let image_receipt = await listing_contract.methods
            .addImage(this.image_url, "dummyhash")
            .send({ from: this.account_id });
          console.log(
            "added image to listing: " + JSON.stringify(image_receipt)
          );

          // Add an address to the listing
          let address_receipt = await listing_contract.methods
            .setStreetAddress(
              this.address1,
              this.address2,
              this.city,
              this.state,
              this.zip
            )
            .send({ from: this.account_id });
          console.log(
            "added street address: " + JSON.stringify(address_receipt)
          );
          // Update listings in vuex
          this.$store.dispatch("update_listings");
        } catch (error) {
          console.log(error);
        }
        // clear form
        this.title = "";
        this.image_url = "";
        this.price = "";
        this.contact = "";
        this.address1 = "";
        this.address2 = "";
        this.city = "";
        this.state = "";
        this.zip = "";
        this.description = "";
      } else {
        //alert("Please fill out the entire form to create a listing.");
      }
    }
  }
};
</script>

<style scoped>
.listings {
  width: 500px;
  margin: auto;
}
</style>
