<template>
  <div>
    <div v-if="bookings && bookings.length == 0">
      <h4>You have no bookings!</h4>
    </div>
    <div v-else-if="bookings">
      <h4>Your Bookings</h4>
      <div v-for="booking in bookings" :key="booking.address">
        <Booking
          :contactInfo="booking.contactInfo"
          :startDate="date(booking.startDate)"
          :endDate="date(booking.endDate)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Booking from "@/components/Booking.vue";

export default {
  components: {
    Booking
  },
  computed: {
    bookings() {
      return this.$store.state.my_bookings;
    }
  },
  methods: {
    date(time) {
      let d = new Date(time);
      return d;
    }
  }
};
</script>