<template>
  <div>
    <div v-if="listings && listings.length == 0">
      <h4>You have no listings!</h4>
    </div>
    <div v-else-if="listings">
      <h4>Your Listings</h4>
      <div v-for="listing in listings" :key="listing.address">
        <Listing
          :address="listing.address"
          :editable="true"
          :title="listing.title"
          :contactInfo="listing.contactInfo"
          :weiPrice="listing.weiPrice"
          :description="listing.description"
          :renter="listing.renter"
          :image="listing.images[0]"
          :streetAddress="listing.streetAddress"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Listing from "@/components/Listing.vue";

export default {
  components: {
    Listing
  },
  computed: {
    listings() {
      return this.$store.state.my_listings;
    }
  }
};
</script>